.color {
  width: 20px;
  height: 20px;
  cursor: pointer;
  padding-left: 2px;
  margin-left: 5px;
  margin-bottom: 5px;
  border: 2px solid white;
}

.selected-color {
  width: 20px;
  height: 20px;
  cursor: pointer;
  padding-left: 2px;
  margin-left: 5px;
  margin-bottom: 5px;
  border: 2px solid #000;
}

#color-menu {
  max-width: 310px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 5px 5px;
}
