#tile-menu {
  max-width: 720px;
}

#tile-container {
  border: 4px solid #ca4f3f;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 10px;
  padding-top: 10px;
  border-radius: 8px;
  max-width: 720px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-right: auto;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
}
