.figure {
  border: 1px dotted #666666;
  cursor: pointer;
}

.selected-figure {
  border: 2px solid red;
}

.text {
  font-size: 16px;
  line-height: 1.5;
  font-family: sans-serif;
  color: #c47c41;
  font-weight: bold;
  text-align: center;
}

#figure-container {
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
  max-width: 310px;
  max-height: 400px;
}

#figure-menu {
  max-width: 310px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 10px 10px;
}
